import React, {Component} from 'react';

class ProjectData extends Component{

  render() {
    return(
        <div className='ProjectData'>
            <p>
                <b>Project Name</b>: {this.props.data[0].name} <br/>
                <b>Project Description</b>: {this.props.data[0].description} <br/>

                <hr/>

                <b>Backlog Items</b> <br/>
                <span>Total Backlog Items: {this.props.data[1].TotalBacklogItems}</span> <br/>
                <span>Completed Backlog Items: {this.props.data[1].CompletedBacklogItems}</span> <br/>
                <span>Pending Backlog Items: {this.props.data[1].PendingBacklogItems}</span> <br/>

                <hr/>

                <b>Bugs</b> <br/>
                <span>Total Bugs: {this.props.data[2].TotalBugItems}</span> <br/>
                <span>Completed Bugs: {this.props.data[2].CompletedBugItems}</span> <br/>
                <span>Pending Bugs: {this.props.data[2].PendingBugItems}</span> <br/>

                <hr/>

                <b>Risk Items</b> <br/>
                <span>Pending Risk Items: {this.props.data[3].PendingRiskItems}</span> <br/>
                <br />
                <b><span>Risk Details</span></b> <br/>
                {
                  this.props.data[3].RiskDetails.map(risk => {
                    return (
                      <span key = {risk.RiskTitle}>
                        Risk Title: {risk.RiskTitle} <br/>
                        Description: {risk.RiskDescription} <br/>
                        <br/>
                      </span>
                    )
                  })
                }

                <hr/> 

                <b>Issues</b> <br/>
                <span>Pending Issues: {this.props.data[3].PendingRiskItems}</span> <br/>
                <br />
                <b><span>Issue Details</span></b> <br/>
                {
                  this.props.data[3].RiskDetails.map(risk => {
                    return (
                      <span key = {risk.RiskTitle}>
                        Issue Title: {risk.RiskTitle} <br/>
                        Description: {risk.RiskDescription} <br/>
                        <br/>
                      </span>
                    )
                  })
                }

            </p>
            <hr/>
        </div>
        
    );
  }
}


export default ProjectData;
