import React, {Component} from 'react';
import ProjectData from './projectdata';
import { GetDashboardDetails_BASE, GetDashboardDetails_API, GetDashboardDetails_API_KEY } from '../Config';

class Homepage extends Component{

  constructor(){
    super();
    this.state = { data: [], loadFlag: false };
  }

  componentDidMount() {
    //var url = GetDashboardDetails_API + "?code=" + GetDashboardDetails_API_KEY; //For Local Host
    var url = GetDashboardDetails_BASE + GetDashboardDetails_API + "?code=" + GetDashboardDetails_API_KEY;
    fetch(url)
    .then(res => res.json())
    .then(json => {
      this.setState({data: json, loadFlag: true});
      console.log(this.state.data);
    })
    .catch(error => console.error('API-ERROR:' + error.toString()));
  }

  render() {
    if(this.state.loadFlag){
      return(
        <div className='container-fluid'>
          <ProjectData data = {this.state.data}/>
          {/*
            this.state.data.map(data => {
              return(
                <div key={project.Title}>
                  <ProjectData project={project}/>
                </div>
              )
            })
          */}
        </div>
      );
    } else {
      return(
        <div>
          FETCHING DATA...
        </div>
      );
    }
    
  }
}
//Created comment

export default Homepage;
